/* ResetPassword.css */

.reset-password-page {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: rgb(231, 230, 230);
}

.reset-password-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background: #ececec;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.reset-password-header {
    text-align: center;
    color: #333;
    font-size: 24px;
    margin-bottom: 20px;
}

.reset-password-instructions {
    text-align: center;
    color: #666;
    margin-bottom: 20px;
}

.reset-password-input {
    background: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 6px;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.reset-password-input img {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

.reset-password-input input {
    flex: 1;
    background: transparent;
    border: none;
    outline: none;
    color: #000;
    font-size: 16px;
}

.reset-password-submit {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: #949494;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    margin-top: 20px;
}

.reset-password-submit:hover {
    background-color: #5e5e5e;
}

.reset-password-login-link {
    text-align: center;
    color: #333;
    font-size: 16px;
    margin-top: 20px;
}

.reset-password-login-option,
.reset-password-signup-option,
.reset-password-forgot-option {
    cursor: pointer;
    color: #424242;
    text-decoration: underline;
    transition: color 0.2s;
}

.reset-password-login-option:hover,
.reset-password-signup-option:hover,
.reset-password-forgot-option:hover {
    color: #6f6f6f;
}

.alert-danger {
    background-color: #dedbdb; /* Grey background color */
    border: 1px solid #000000; /* Black border */
    color: #494949; /* White text color */
    padding: 10px; /* Padding around the message */
    border-radius: 5px; /* Rounded corners */
    font-weight: bold; /* Bold text */
    text-align: center; /* Center the text */
    font-size: 20px; /* Font size */
    margin-top: 10px; /* Space between the error message and other content */
  }