/* Styles for the OTP verification popup */
/* Styles for the OTP verification popup container */
.otp-verification-container {
  display: none; /* Initially hide the container */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent dark background */
  z-index: 1001; /* Ensure it appears above other content, including the popup */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Styles for the OTP verification popup */
.otp-verification-popup {
  position: relative;
  background-color: #f0f0f0; /* Light gray background for the popup */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); /* Box shadow for the popup */
  max-width: 300px;
  text-align: center;
  z-index: 1002; /* Ensure it appears above the overlay */
}

/* Add the rest of your styles for OTP input, button, etc. */


.close-cross {
  position: absolute;
  right: 0px;
  top: -10px;
  cursor: pointer;
  font-size: 15px;
  padding: 10px;
}

.close-cross:hover {
  color: #2f2e2e;
}

.otp-verification-header p {
  margin-bottom: 10px;
  padding: 0;
}


.otp-input {
  margin-bottom: 20px;
}

/* Style for the OTP input field */
.otp-input input {
  /* width: 100%; */
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  outline: none;
}

/* Style for the Verify button */
.verify-button {
  background-color: #676868; /* Blue color for the button */
  color: #fff; /* White text color */
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.verify-button:hover {
  background-color: #3d3d3d; /* Darker blue color on hover */
}
