:root {
    --signup-white: #424242; 
  }
  
  .signup-page {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: linear-gradient(#e3e3e3c2, #b2b2b2);
    background: rgb(231, 230, 230);
  }
  
  .signup-container {
     max-width: 700px; 
     background: #ececec;
    padding: 20px;
    border-radius: 10px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border: 1px solid white;
  }
  .signup-header {
    text-align: center;
    color: #333; 
  }
  
  .signup-tabs {
    display: flex;
    justify-content: center;
    gap: 50px;
  }
  
  .signup-tab {
    cursor: pointer;
    font-size: 24px;
    color: #333; 
    padding: 10px;
    transition: all 0.2s;
    flex: 1; /* Each tab takes an equal portion of the available space */
    text-align: center;
  }
  
  .signup-tab.active {
    border-bottom: 2px solid var(--signup-white); 
  }
  
  .signup-social-buttons {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
  }
  
  .social-button {
    display: flex;
    align-items: center;
    background-color: #d3d3d3; 
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.2s;
  }
  
  .social-button:hover {
    background-color: #708090; 
  }
  
  .social-icon-circle {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 8px;
    border: 2px solid #fff;
  }
  
  .social-icon {
    width: 100%;
    height: 100%;
  }
  
  .signup-line {
    text-align: center;
    color: #333; 
    margin: 16px 0;
    font-size: 20px;
  }
  
  .signup-line:before,
  .signup-line:after {
    content: "";
    flex: 1;
    border-top: 1px solid #ccc; 
  }
  
  .signup-input-fields {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center; 
    align-items: center;
    margin-top: 25px;
  }
  

  .login-input {
    background: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 6px;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    width: 90%;
}

.login-input img {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

.login-input input {
    flex: 1;
    background: transparent;
    border: none;
    outline: none;
    color: #000;
    font-size: 16px;
}
  
  .signup-submit-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px auto;
  }
  
  .signup-submit {
    width: 100%;
    height: 50px;
    background-color: #949494;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    text-align: center;
    line-height: 50px; 
    cursor: pointer;
    transition: all 0.2s;
  }
  
  
  .signup-submit:hover {
    background-color: #5e5e5e; 
  }
  
  .signup-login-link {
    text-align: center;
    color: #333; 
    font-size: 16px;
    margin-top: 20px;
  }
  
  .signup-login-option,
  .signup-signup-option,
  .signup-forgot-option {
    cursor: pointer;
    color: var(--signup-white); 
    text-decoration: underline;
    transition: color 0.2s;
  }
  
  .dont-have-acc-option{
    margin-top: 20px;
  }
  
  .signup-login-option:hover,
  .signup-signup-option:hover,
  .signup-forgot-option:hover {
    color: #adadae; 
  }
  
  
 